import {
  BrowserRouter as Router,
  Switch,
  Route,
} from "react-router-dom";
import BatchCalendar from "./routes/BatchCalendar";
import Detail from "./routes/Detail";

function App() {
  return (
    <Router>
      <Switch>
        <Route path="/abot-us">
          <h1>Hello</h1>
        </Route>
        <Route path="/movie/:id">
          <Detail />
        </Route>
        <Route path="/">
          <BatchCalendar />
        </Route>
      </Switch>
    </Router>
  ); 
}

export default App;